.modal {
    display: none;
    position: fixed;
    z-index: 2;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgb(0,0,0);
    background-color: rgba(0,0,0,0.4);
}
.close {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
}
.close:hover,
.close:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
}

/* Modal Header */
.modal-header {
    padding: 2px;
    background-color: #444444;
    color: white;
}

/* Modal Body */
.modal-body {
    padding-right: 12px;

}

/* Modal Content */
.modal-content {
    position: absolute;
    z-index: 3;
    background-color: #fefefe;
    margin: auto;
    padding: 0;
    border: 1px solid #888;
    border-radius: 12px;
    left: 20%;
    width: 60%;
    top: 90px;
    -webkit-animation-name: animatetop;
            animation-name: animatetop;
    padding: 20px;

    -webkit-animation-duration: 0.4s;

            animation-duration: 0.4s;
}
