header {
    position: fixed;
    height: 80px;
    width: 100%;
    top: 0;
    -webkit-transition: all .5s ease-out 0.1s;
    -moz-transition: all .5s ease-out 0.1s;
    -o-transition: all .5s ease-out 0.1s;
    transition: all .5s ease-out 0.1s;
    z-index: 1;
    background-color: rgba(12, 9, 4, 0.9);
}
.nav-up {
    background-color: rgba(12, 9, 4, 0);
}
#logo {
    padding-left: 50px;
    width: 80px;
    height: 80px;
    float: left;
}

#profile {
    width: 30px;
    height: 30px;
}

header > a {
    position: relative;
    display: inline-block;
    color: white;
    text-decoration: none;
    font-weight: bold;
    font-size: 20px;
    padding: 15px;
    margin: 10px;
    line-height: 25px;
    border-radius: 4px;
    transition: all 0.2s;
    float: right;
}
header > a:hover {
    background-color: #ddd;
    color: black;

}

header > input[type="text"] {
    -webkit-border-radius: 35px;
    -moz-border-radius: 35px;
    border-radius: 35px;
    background-color: rgba(255,255,255,0.1);
    padding: 10px;
    margin: 20px;
    max-width: 600px;
    width: 100%;
}

header > button {
    -webkit-border-radius: 35px;
    -moz-border-radius: 35px;
    border-radius: 35px;
    margin: 20px;
    padding: 10px;
    margin-left: -50px;
    cursor: pointer;
}