.content {
    margin: auto;
    margin-top: 90px;
    box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.5);
    max-width: 640px;
    overflow: hidden;
}

.form > label {
    margin: 10px;
    font-size: 18px;
    float: left;
    font-weight: bold;
}

.duration {
    font-weight: normal;
    font-size: 18px;
    margin: 10px;
    float: left;
    color: rgb(85, 85, 85);
}

.input {
    width: 90%;
    padding: 12px 20px;
    margin: 8px 0;
    box-sizing: border-box;
    border: 3px solid #ccc;
    -webkit-transition: 0.5s;
    transition: 0.5s;
    outline: none;
}
  
.form input[type=text]:focus, input[type=number]:focus, .textarea:focus {
    border: 3px solid #555;
}


.textarea {
    width: 90%;
    padding: 12px 20px;
    margin: 8px 0;
    box-sizing: border-box;
    border: 3px solid #ccc;
    height: 100px;
    resize: none;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}

[type=radio] + img {
    cursor: pointer;
    display: block;
}

.menu {
    position: absolute;
    z-index: 2;
    top: 80px;
    right: 0px;
    max-width: 300px;
    height: 300px;
    background-color: white;
    overflow: scroll;
}

.img {
    display: block;
    height: 80px;
    width: auto;
    margin: 10px;
    cursor: pointer;
}

.details {
    display: flex;
    align-items:center;
    margin: 10px;
    border-bottom: 1px solid grey;
 }

.horizontalScroll {
    width: 120px;
    height: 500px;
    overflow-y: scroll;
    overflow-x: hidden;
    transform: rotate(-90deg) translateY(-50px);
    transform-origin: right top;
}

.rotated {
  transform: rotate(90deg) translateX(100px);
  transform-origin: right top;
  padding:5px;
  height: 90px;
}

