.content {
    margin: 90px;
    box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.5);
    max-width: 1640px;
    overflow: hidden;
}

.img {
    vertical-align: top;
    display: inline-block;
    width: 46%;
    margin: 20px 30px 0 0;
}

.form {
    display: inline-block;
    width: 46%;
}

.form label {
    font-size: 18px;
    float: left;
    font-weight: bold;
}

.input {
    width: 90%;
    padding: 12px 20px;
    margin: 8px 0;
    box-sizing: border-box;
    border: 3px solid #ccc;
    -webkit-transition: 0.5s;
    transition: 0.5s;
    outline: none;
}
  
.form input[type=text]:focus, input[type=number]:focus, .textarea:focus {
    border: 3px solid #555;
}

.textarea {
    width: 90%;
    padding: 12px 20px;
    margin: 8px 0;
    box-sizing: border-box;
    border: 3px solid #ccc;
    height: 100px;
    resize: none;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}

.half_box, select {
    width: 200px;
    padding: 12px 20px;
    margin: 8px 6px;
    box-sizing: border-box;
    border: 3px solid #ccc;
    -webkit-transition: 0.5s;
    transition: 0.5s;
    outline: none;
}