
.App {
  font-family: 'Source Sans Pro';
  text-align: center;
}
.round {
  position: absolute;
  width: 40px;
  border-radius: 50%;
  font-size: 30px;
  background-color: #f1f1f1;
  color: #242124;
  z-index: 1;
}
.round:hover {
  background-color: #ddd;
  color: #242124;
}
.left {
  top: 40%;
  left: 0px;
}
.right {
  top: 40%;
  right: 10px;
}
.gallery-container {
  position: fixed;
}

.text-overlay {
  position: fixed;
  width: 100%;
  left: 50%;
  transform: translate(-50%, 0);
}
div > h2 {
  color: white;
  padding-top: 120px;
  font-size: 60px;
}
.text-overlay > h3 {
  color: white;
  font-size: 40px;
}
.blinking-cursor {
  font-weight: 200;
  font-size: 40px;
  color: white;
  -webkit-animation: 1s blink step-end infinite;
  -moz-animation: 1s blink step-end infinite;
  -ms-animation: 1s blink step-end infinite;
  -o-animation: 1s blink step-end infinite;
  animation: 1s blink step-end infinite;
}

@keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color: white;
  }
}

@-moz-keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color: white;
  }
}

@-webkit-keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color: white;
  }
}

@-ms-keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color: white;
  }
}

@-o-keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color: white;
  }
}