.modal {
    display: none;
    position: fixed;
    z-index: 2;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgb(0,0,0);
    background-color: rgba(0,0,0,0.4);
}
.close {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
}
.close:hover,
.close:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
}

/* Modal Header */
.modal-header {
    padding: 2px;
    background-color: #444444;
    color: white;
}

/* Modal Body */
.modal-body {
    padding-right: 12px;

}

/* Modal Content */
.modal-content {
    position: absolute;
    z-index: 3;
    background-color: #fefefe;
    margin: auto;
    padding: 0;
    border: 1px solid #888;
    border-radius: 12px;
    left: 20%;
    width: 60%;
    top: 90px;
    animation-name: animatetop;
    padding: 20px;

    animation-duration: 0.4s;
}
.show-popup {
    display: block;
}

.hide-popup {
    display: none;
}

.file-drop {
    border: 4px dashed grey;
    border-radius: 6px;
    width: 100%;
    text-align: center;
    padding: 5px;
}

.file-drop img {
    margin-top: 10vh;
}

.file-button {
    width: 200px;
    display: table;
    background-color: orangered;
    cursor: pointer;
    margin: auto;
    
    font-size: 20px;
    color: white;
    padding: 15px;
    border-radius: 6px;
    transition: all 0.2s;
}

.file-button:hover {
    background-color: rgb(211, 77, 0);
}
.file-button input[type="file"] {
    display: none;
}
/* Add Animation */
@keyframes animatetop {
    from {top: -100%; opacity: 0}
    to {top: 0; opacity: 1}
}