
.text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  list-style: none;
  border-bottom: 0;
}

.text.hidden {
  border-bottom: 1px solid #fff;
}

.text li {
  display: inline-block;
  float: left;
  font-weight: 700;
  font-size: 2em;
  color: #fff;
  opacity: 1;
  transition: all 0.8s ease-in-out;
  max-width: 2em;
}
.text.hidden li.spaced {
  padding-left: 0;
}
.text li.spaced {
  padding-left: 0.5em;
}

.text.hidden li.ghost {
  opacity: 0;
  max-width: 0;
}

span {
  font-family: 'Open Sans', sans-serif;
  font-size: 15px;
}

h1 {
  font-size: 32px;
  text-decoration: none;
  font-weight: normal;
}

h1:before{
  content: "";
  position: absolute;
  width: 100px;
  height: 40px;
  bottom: -100;
  border-bottom: 2px solid orangered;

  -webkit-animation: mymove 2s 1;
  animation: mymove 2s 1;
}

@-webkit-keyframes mymove {
  from {width: 0px;}
  to {width: 100px;}
}

@keyframes mymove {
  from {width: 0px;}
  to {width: 100px;}
}