.expo-content {
    display: inline-block;
    background-color: rgb(245, 245, 245);
    margin: 10px;
    margin-top: 100px;
    width: 35%;
}
.expo-content h2 {
    display: block;
    text-decoration: none;
    font-size: 15px;
}
.expo-item {
    margin: 5px;
    padding: 5px;
    box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.5);
    display: inline-block;
    transition: box-shadow .175s ease-out,opacity .175s ease-out;
    min-width: 100px;
    min-height: 100px;
    position: relative;
}
.expo-item:hover {
    box-shadow: 0 10px 50px 0 rgba(0, 0, 0, 0.5);
}

.expo-img {
    display: block;
    width: 50px;
    position: absolute;               
    top: 50%;                    
    left: 50%;                         
    transform: translate(-50%, -50%);
}

.expo-content > h1 {
    font-size: 28px;
    font-weight: bold;
    display: inline-block;
}

.expo-content > button {
    font-family: 'Source Sans Pro';
    font-size: 20px;
    color: white;
    background-color: rgb(255, 81, 0);
    padding: 15px;
    border-radius: 12px;
    transition: all 0.5s;
}

.expo-content > button span:after {
    content: '\00bb';
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    transition: 0.5s;
}
.expo-content > button span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.5s;
  }
.expo-content > button:hover span {
    padding-right: 25px;
}
  
.expo-content > button:hover span:after {
    opacity: 1;
    right: 0;
}

.expo-content a {
    text-decoration: none;
    color: white;
    padding: 25px;
}